.navbar-wrapper{
    background-color: white;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.1);
    padding: 1rem;
    position: fixed;
    width: calc(100vw - 2rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    top: 0;
    z-index: 5;
}
.menu-bars{
    font-size: 2rem;
}
.menu-bars:hover{
    cursor: pointer;
}
.logo{
    font-size: 2rem;
    color: darkviolet;
}
.logo-full{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 8rem;
    text-decoration: none;
}
.text-logo{
    color: black;
}
.left-navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 12rem;
}
.button-for-theme{
    background-color: white;
    border-color: black;
}
.leftsidebar2{
    background-color: white;
    padding: 1rem;
    box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.1);
    width: 13rem;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 7;
}
.leftsidebar2.active1 {
    left: 0;
    transition: 100ms;
}
.menu-leftsidebar{
    display: flex;
    margin-top: 2rem;
    justify-content: flex-start;
    align-items: center;
    text-decoration: none;
    color: black;
}
.menu-leftsidebar:hover{
    color: darkviolet;
}
.active{
    color: darkviolet;
}
.text-menu-leftsidebar{
    margin-left: 1rem;
}
.logo-menu-leftsidebar{
    font-size: 1rem;
    display: flex;
    width: 1.5rem;
    height: 1.5rem;
    justify-content: center;
    align-items: center;
}
.shadow{
    display: none;
}
.shadow.active1{
    display: flex;
    top: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.3);
    position: fixed;
    z-index: 6;
}
.image-profile{
    margin-top: 4rem;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}
.menu-leftsidebar-wrapper{
    margin-top: 1rem;
}